import { Badge } from 'vant';
export default {
  name: 'GachaSimulatorCard',
  components: {
    [Badge.name]: Badge
  },
  props: {
    isSingle: Boolean,
    name: String,
    star: Number,
    isNew: Boolean,
    count: Number,
    inList: Boolean,
    ani: Number,
    shining: Boolean,
    index: Number
  },
  data() {
    return {
      stars: this.$t('gacha.stars')
    };
  },
  computed: {
    animationDelay() {
      switch (this.ani) {
        default:
          return 0;
        case 1:
          return `${750 * this.index}ms`;
        case 2:
          return `${500 * this.index}ms`;
      }
    }
  }
};